.navbar-dark {
  background-color: #675956;
}
.footer {
  background-color: #a59693;
  margin: 0px auto;
  padding: 20px 0px 20px 0px;
}
.photo {
  max-width: auto;
}
.photo img {
  width: 100%;
  height: 45vh;
}
.photo2 {
  max-width: auto;
}
.photo2 img {
  width: 100%;
  height: 43vh;
}

.text-position {
  color: white;
  font-weight: 700;
  position: absolute;
  bottom: 50px;
  font-size: 25px;
  text-transform: capitalize;
}
.text-position2 {
  color: white;
  font-weight: 500;
  position: absolute;
  bottom: 15px;
  font-size: 18px;
  text-transform: capitalize;
}
.detail-img {
  width: 100%;
  height: auto;
}
@media (max-width: 768px) {
  .text-position {
    font-size: 15px;
    bottom: 24px;
  }
  .text-position2 {
    font-size: 13px;
    bottom: 6px;
  }
}
.titling {
  font-weight: bolder;
  text-decoration: underline;
  text-transform: capitalize;
  font-size: 18px;
}

.linking {
  text-decoration: none;
  color: black;
}
.paragraph {
  font-weight: bold;
}
